/* In your CSS file or within a style tag */
.bg-orange {
  background-color: #ff6a00;
}

.expanded-row {
  display: inline-block !important;
}

.expanded-row-hidden {
  display: none !important;
}

.name:hover {
  cursor: pointer; /* or any other cursor value you prefer, like 'pointer' */
}

.gray-row {
  background-color: rgba(128, 128, 128, 0.37) !important; /* or any other shade of gray */
}

.btn-xs {
  padding: 0.20rem 0.5rem;
  font-size: 0.65rem;
}

